.item {
  position: relative;
  display: flex;
  margin: 15px 0;
  border-bottom: 1px solid #ccc;
  max-width: 500px;
}

.message {
  color: #333;
  font-size: 14px;
  font-family: Tahoma, sans-serif;
  padding-left: 15px;
}

.iconBox {
  width: 50px;
}

.icon {
  width: 100%;
  height: auto;
}

.like {
  display: flex;
  align-items: center;
  position: absolute;
  top: 5px;
  right: 5px;
  font-family: Tahoma, sans-serif;
  font-size: 12px;
  height: 20px;
  width: 40px;
  background: url('../../../../../img/icon-like.svg') no-repeat left center / 20px 20px;
  padding-left: 25px;
  cursor: pointer;
}